<template>
    <div class="data-editor-phone">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-form :model="form" label-position="left" label-width="100" ref="form">
                <mu-form-item class="p-15 border-1px-b" prop="phone" label="手机号码" :rules="phoneRules">
                    <mu-text-field v-model="form.phone" placeholder="请输入手机号码" prop="phone" solo/>
                </mu-form-item>
                <mu-form-item class="p-15 border-1px-b" prop="code" label="验证码" :rules="codeRules">
                    <div class="flex aic jcc mn">
                        <mu-text-field v-model="form.code" placeholder="请输入验证码" prop="code" solo/>
                        <mu-button class="mn" :disabled="validate.disabled" color="primary"
                                   @click="getValidate" small
                                   flat>{{validate.text}}
                        </mu-button>
                    </div>
                </mu-form-item>
            </mu-form>
            <mu-button class="db ma w90 background-primary" color="#fff" @click="submit" v-loading="loading"
                       data-mu-loading-size="24"
                       :disabled="loading"
                       flat>提交
            </mu-button>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {validate} from "../../../types/index"
    import {Validate} from "../../../storage"
    import {resetPhone} from "../../../api/user"
    import {getValidate} from "../../../api/user"

    export default {
        name: "phone",
        data() {
            return {
                /***
                 * 表单验证
                 */
                phoneRules: [
                    {validate: val => !!val, message: '必须填写手机号码'},
                    {validate: val => /^1\d{10}$/.test(val), message: '请输入正确的手机号码'}
                ],
                codeRules: [
                    {validate: val => !!val, message: '必须填写验证码'},
                    {validate: val => /^\d{4,6}$/.test(val), message: '请输入正确的验证码'}
                ],
                /***
                 * 表单内容
                 *      phone: 手机号码
                 */
                form: {
                    phone: '',
                    code: ''
                },
                /***
                 * 验证码
                 */
                validate: {
                    disabled: false,
                    text: '获取验证码',
                    timer: null,
                    type: 1
                },
                loading: false
            }
        },
        computed: {
            /***
             * 导航栏标题
             * @return {string}
             */
            navigateTitle() {
                return this.$route.query.phone ? '修改手机号码' : '绑定手机号码'
            }
        },
        activated() {
            this.$refs.form.clear()

            this.form = {
                phone: '',
                code: ''
            }
        },
        methods: {
            /***
             * 提交 更换手机号码 表单验证
             */
            submit() {
                this.$refs.form.validate().then(result => {
                    if (result) {
                        this.loading = true
                        resetPhone(this.form).then(res => {
                            this.loading = false

                            const info = Object.assign({}, this.user, {phone: this.form.phone})

                            this.login(info)

                            this.$router.replace('/about/data')
                        }).catch(err => {
                            this.loading = false
                            this.$toast('手机号码更换失败')
                        })
                    }
                });
            },
            // 获取验证码
            getValidate() {
                getValidate(this.validate.type, this.form.phone).then(res => {
                    validate(this.validate)
                })
            }
        },
        watch: {
            'form.phone'(value) {
                this.validate.phone = value

                validate(this.validate, true)
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus">
    .data-editor-phone
        padding-top 46px

</style>



